import React from 'react'
import './Gallery.css'
import Grid  from '@mui/material/Grid'
import {motion} from 'framer-motion'

import image5 from '../../images/270539004_119446247246644_1102086762450142202_n.webp'
import image6 from '../../images/272214936_1081037725778629_1222534997021757023_n.webp'
import image7 from '../../images/272068680_272346478329236_3928953709977439528_n.webp'

const Gallery = () => {
  return (
    <div>
        <motion.div className="gallery"
        whileInView={{ x: [-150, 0] }}
        transition={{ ease: "easeIn", duration: 1 }}
        > <span>GALLERY</span></motion.div>
        <div className='hr'><hr /></div>
      <div className="gallery">
      <Grid className='container-fluid ' spacing={2} lg={12} container item>
      <Grid md={12} lg={12} sm={12} xs={12} xl={12}></Grid>
       
        <Grid lg={4} xl={4} md={4} xs={12} sm={6}><div className="image2"><img src={image7} alt="" /></div></Grid>
        
        <Grid lg={4} xl={4} md={4} xs={12} sm={6}><div className="image2"><img src={image5} alt="" /></div></Grid>
        <Grid lg={4} xl={4} md={4} xs={12} sm={6}><div className="image2"><img src={image6} alt="" /></div></Grid>
        
      </Grid>
      </div>
    </div>
  )
}

export default Gallery
