import React from 'react'
import './Testimonials.css'
import { Grid } from '@mui/material'

import { motion } from 'framer-motion'
import Card from 'react-bootstrap/Card'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { github } from '@fortawesome/free-solid-svg-icons'

const Testimonials = () => {
  return (
    <div>
      <motion.div
        className="gallery"
        whileInView={{ x: [-150, 0] }}
        transition={{ ease: 'easeIn', duration: 1 }}
      >
        {' '}
        <span>QUOTE'S</span>
      </motion.div>
      <div className="hr">
        <hr />
      </div>
      <div className="container">
        <Grid lg={12} container item spacing={2}>
          <Grid lg={12} xl={12} md={12} sm={12} xs={12}>
            <div className="testimg">
              {' '}
              <img
                src="https://m.media-amazon.com/images/M/MV5BYWU4ZTQ1ZTktZTc3Mi00MzllLThiN2QtNjc1ZjY5ZGQxNDdjL2ltYWdlL2ltYWdlXkEyXkFqcGdeQXVyMTc4MzI2NQ@@._V1_.jpg"
                alt=""
              />
            </div>
          </Grid>
          <Grid lg={12} xl={12} md={12} sm={12} xs={12}>
            <motion.div
              className="quote"
              whileInView={{ y: [-10, 0] }}
              transition={{ ease: 'easeIn', duration: 0.8 }}
            >
              <q>
                Perfection is achieved, not when there is nothing more to add,
                but when there is nothing left to take away.
              </q>
            </motion.div>
            <motion.div
              className="auther"
              whileInView={{ x: [-10, 0] }}
              transition={{ ease: 'easeIn', duration: 0.8 }}
            >
              - Antoine de Saint-Exupery
            </motion.div>
          </Grid>
        </Grid>
      </div>
      
    </div>
  )
}

export default Testimonials
