import React from 'react'
import Button from 'react-bootstrap/Button'
import Container from 'react-bootstrap/Container'
import Form from 'react-bootstrap/Form'
import Nav from 'react-bootstrap/Nav'
import Navbar from 'react-bootstrap/Navbar'
import NavDropdown from 'react-bootstrap/NavDropdown'
import './Navbars.css'

const Navbars = () => {
  return (
    <div>
      <Navbar bg="dark"  expand="md" className='navbar'>
        <Container fluid>
          <Navbar.Brand href="#" className="text-dark"><div className="sreeramachandran">Sreeramachandran</div></Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarScroll"  />
          <Navbar.Collapse id="navbarScroll" >
            <Nav
              className="me-auto my-2 my-lg-0 "
              style={{ maxHeight: '140px' }}
              navbarScroll
            >
              <Nav.Link  className="toggle text-warning">
                Home
              </Nav.Link>
              <Nav.Link href="#action2" className="toggle text-warning">
                connect with me
              </Nav.Link>
              <Nav.Link href="#action2" className="toggle text-warning">
                Gallery
              </Nav.Link>
              <div className="dropdown ">
              <NavDropdown
                className='text-secondary'
                title="Projects"
                id="navbarScrollingDropdown"
              >
                <NavDropdown.Item href="#action3">
                  Static Website
                </NavDropdown.Item>
                <NavDropdown.Item href="#action4">
                  Water Quality Monioring System
                </NavDropdown.Item>
                <NavDropdown.Item href="#action5">
                  Smart E Waste Bin
                </NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action5">Seminar</NavDropdown.Item>
              </NavDropdown>
              </div>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </div>
  )
}

export default Navbars
