import React from 'react'
import './Iam.css'
import Grid from '@mui/material/Grid'

import { motion } from "framer-motion"
import TypeWriterEffect from 'react-typewriter-effect';
const Iam = () => {
  return (
    <div className='container position1'>
      <div className='grid'>
        <Grid lg={12} container item spacing={2}>
        <Grid lg={4} xl={4} md={12} xs={12} sm={12} ><motion.div className='hello'
        whileInView={{ y: [-50, 0] }}
        transition={{ ease: "easeIn", duration: 0.8 }}
        ><span>Hello EveryOne</span></motion.div></Grid>
        </Grid>
        <Grid lg={12} container item spacing={2}>
          
          <Grid lg={3} xl={2} md={6} xs={12} sm={8} >
            <div className="name">
            <TypeWriterEffect
        textStyle={{
          fontFamily: 'Cabin Sketch',
          color: 'rgb(202 67 119)',
          fontWeight: 300,
          
          fontSize: '1.5em',
        }}
        startDelay={2000}
        cursorColor="#3F3D56"
        multiText={[
          
          "I'AM SREERAMACHANDRAN.N",
          "I'AM an Electronics and Communication engineer ",
          "I'AM a Web Developer ...",
          "I'AM a Self Learner ...",
          "I'AM SREERAMACHANDRAN.N"
        ]}
        loop={true}
        nextTextDelay={1000}
        typeSpeed={30}
      />
            </div>
          </Grid>
        </Grid>
      </div>
    </div>
  )
}

export default Iam
